import React from 'react';
import {
    Select
} from 'antd';
import EditingType from '@/constant/EditingType';
import { SearchOutlined } from '@ant-design/icons';

const {
    Option
} = Select;

export default class SearchNode extends React.Component {
    onChange = (value) => {
        this.props.updateSearchedNode(value);
        this.props.updateEditingType(EditingType.SEARCH_NODE);
    }

    render() {
        const { intl, initDone, nodes = [] } = this.props;

        return (
            <Select
                showSearch
                allowClear
                suffixIcon={<SearchOutlined />}
                size='small'
                style={{ width: '100%', margin: '20px 0 20px 0' }}
                placeholder={initDone && intl.get("Search Node")}
                optionFilterProp="children"
                onChange={
                    this.onChange
                }
                filterOption={
                    (input, option) => {
                        return option.value.toLowerCase().indexOf(input.toLowerCase()) >= 0
                    }
                } >
                {nodes.map(node =>
                    <Option value={node} key={node}> {node} </Option>
                )}
            </Select>
        );
    }
}